import { Inline, Node } from '@ali/4ever-cangjie';
import { JsonML } from '@ali/4ever-utils';
import Mention from '../models';

export default {
  name: 'mention',

  match(node: Node): boolean {
    return Mention.isMention(node);
  },

  convert(node: Inline): JsonML {
    // @ts-ignore
    return ['span', { ...node.data, 'data-type': 'mention' }];
  },
};
